import Vue from 'vue'
import Loading from './skeleton-loading'

const LoadingCon = Vue.extend(Loading)

const init = (selector, top = '44px') => {
  const instance = new LoadingCon({
    propsData: {
      selector,
      top
    }
  })

  instance.vm = instance.$mount()

  document.querySelector(selector).appendChild(instance.vm.$el)

  return instance.vm
}

export default {
  install(Vue) {
    Vue.prototype.$skeLoading = init
  }
}
