<template>
  <div id="app">
    <transition :name="routeAni">
      <keep-alive include="store,seller-goods-add,place-order,order-list,seller-order-list,personal,seller-refunds">
        <router-view class="Router"/>
      </keep-alive>
    </transition>
  </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'
import { post, getSearch } from './common/js/http'
import { setItem } from './common/js/storage'

export default {
  data() {
    return {
      routeAni: '',
      times: 0,
      prevKey: null
    }
  },
  computed: {
    ...mapState(['routeHistory'])
  },
  mounted () {
    window.addEventListener('popstate', (e) => {
      this.$router.isBack = true
    })
  },
  created() {
    const search = getSearch()
    if (search.uid) {
      setItem('token', search.token)
      setItem('uid', search.uid)
    }
    post('/webapi/config/getCommonConfig')
      .then(({ data: { data } }) => {
        this.changeConfig(data.config)
      })
  },
  methods: {
    ...mapMutations(['changeHistory', 'changeConfig'])
  },
  watch: {
    $route(to, from) {
      this.times++
      if (this.times === 1) return
      const isBack = this.$router.isBack
      if (isBack) {
        // this.changeHistory(this.routeHistory.slice(0, this.routeHistory.length - 1))
        this.routeAni = 'slide-right'
      } else {
        const children = this.$children
        for (let i = 0; i < children.length; i++) {
          if (to.name === children[i].$options.name && children[i].$refs.scroll) {
            children[i].$refs.scroll.scrollTo([0, 0])
          }
        }
        this.changeHistory([...this.routeHistory, to.name])
        this.routeAni = 'slide-left'
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
  html
    font-family: 'Source Sans Pro', -apple-system, BlinkMacSystemFont, 'Segoe UI',
      Roboto, 'Helvetica Neue', Arial, sans-serif;
  body
    height 100vh
    font-family Microsoft YaHei, PingFang SC !important
  .Router
    font-family: 'Source Sans Pro', -apple-system, BlinkMacSystemFont, 'Segoe UI',
      Roboto, 'Helvetica Neue', Arial, sans-serif;
    position fixed !important
    width 100%
    height 100vh
    // -webkit-overflow-scrolling touch
    transition all .35s ease-in-out
    top 0
    left 0
    background #fff
  .slide-left-enter, .slide-right-leave-active
    opacity 0
    transform translate3d(100%, 0, 0)
  .slide-left-leave-active, .slide-right-enter
    opacity 0
    transform translate3d(-100%, 0, 0)
</style>
