import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    defaultAvatar: require('./moren@2x.png'),
    routeHistory: [],
    appConfig: {},
    userInfo: {}
  },
  mutations: {
    changeHistory(state, list) {
      state.routeHistory = list
    },
    changeConfig(state, data) {
      state.appConfig = data
    },
    changeUserInfo(state, info) {
      state.userInfo = info
    }
  },
  actions: {
  },
  modules: {
  }
})
