<template>
  <div class="skeleton-loading" :style="{top: top}">
    <van-skeleton loading row="30"></van-skeleton>
  </div>
</template>

<script>
export default {
  name: 'skeleton-loading',
  props: {
    selector: {
      type: String,
      default: ''
    },
    top: {
      type: String,
      default: '44px'
    }
  },
  methods: {
    close() {
      if (this.$el) {
        document.querySelector(this.selector).removeChild(this.$el)
        this.$destroy()
      }
    }
  }
}
</script>

<style scoped lang="stylus">
.skeleton-loading
  position absolute
  padding-top 20px
  left 0
  top 0
  bottom 0
  right 0
  overflow hidden
  background #fff
  z-index 999
</style>
