import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import 'lib-flexible'
import SkeletonLoading from './components/skeleton-loading'
import './assets/css/reset.css'
import VConsole from 'vconsole'
import nativeJsCall from './common/js/native'

import { List, Icon, Toast, Loading, Swipe, SwipeItem, Rate, Stepper, Checkbox, Radio, RadioGroup, Switch, Popup, Picker, PullRefresh, Image as VanImage, Skeleton, Dialog, CountDown } from 'vant'

Vue.use(List)
Vue.use(Icon)
Vue.use(Loading)
Vue.use(Swipe)
Vue.use(SwipeItem)
Vue.use(Loading)
Vue.use(Rate)
Vue.use(Stepper)
Vue.use(Checkbox)
Vue.use(Radio)
Vue.use(RadioGroup)
Vue.use(Switch)
Vue.use(Popup)
Vue.use(Picker)
Vue.use(PullRefresh)
Vue.use(VanImage)
Vue.use(Skeleton)
Vue.use(Dialog)
Vue.use(SkeletonLoading)
Vue.use(CountDown)
Vue.prototype.$toast = Toast

Vue.prototype.$jsCallNative = nativeJsCall

if (process.env.NODE_ENV === 'development') {
  // eslint-disable-next-line no-new
  new VConsole()
}
Vue.config.productionTip = false

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
