import axios from 'axios'
import { Toast } from 'vant'
import { getItem } from './storage'
import nativeJsCall from './native'

export function getSearch() {
  const query = window.location.search.substring(1)
  var vars = query.split('&')
  const res = {}
  for (var i = 0; i < vars.length; i++) {
    var pair = vars[i].split('=')
    res[pair[0]] = pair[1]
  }
  return res
}

const instance = axios.create({
  // baseURL: 'http://live.nasinet.com',
  timeout: 5000,
  withCredentials: true
})

instance.interceptors.response.use((res) => {
  if (res.data.msg === '登录超时，请重新登录') {
    Toast.fail(res.data.msg)
    nativeJsCall('logout')
    return Promise.reject(new Error('登陆超时'))
  }
  if (res.data.msg) {
    Toast.fail(res.data.msg)
    return Promise.reject(new Error(res.data.msg))
  }
  return res
}, (err) => {
  Toast.fail('请求超时，请稍后再试')
  return Promise.reject(err)
})

export const post = (url, data) => {
  const uid = getSearch().uid || getItem('uid')
  const token = getSearch().token || getItem('token')
  if (!data) data = {}
  data.token = decodeURIComponent(token)
  data.uid = uid
  return instance.post(url, data)
}
