<template>
  <div class="back-header" style="height: 44px">
    <van-icon @click="goBack" class="back-btn" name="arrow-left" />
    <h4>{{ title }}</h4>
    <div class="right-slot">
      <slot/>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import nativeJsCall from '../../common/js/native'

export default {
  name: 'back-header',
  props: {
    title: {
      type: String,
      default: ''
    },
    canBack: {
      type: Boolean,
      default: true
    },
    delta: {
      type: Number,
      default: -1
    }
  },
  computed: {
    ...mapState(['routeHistory'])
  },
  methods: {
    goBack() {
      if (!this.canBack) {
        nativeJsCall('popWebView')
        return
      }
      this.$router.goBack(this.delta)
    }
  }
}
</script>

